<template>
    <div>
        <div class="fx-s">
            <p class="w-45">
                <b></b><span>{{ one.training_date }}</span>
            </p>
            <p class="w-65">
                <b></b><span>{{ one.member_num }}</span>
            </p>
        </div>
        <p>
            <b></b><span v-for="(v, i) in one.items" :key="i">
                {{ v.name }}
            </span>
        </p>
        <p>
            <b></b><span>{{ one.title }}</span>
        </p>
    </div>
</template>

<script>
export default {
    props: {
        one: {
            type: Object,
            default() {
                return {
                    training_date: '2022-12-12 12:12',
                    member_num: 22,
                    items: [ { name: '' }, { name: '' } ],
                    title: ''
                }
            }
        }
    }
}
</script>

<style>

</style>


