import PanelDef from '../../../../funcks/ui/panel/PanelDef.vue'
import UiTableOpera from '../../../../funcks/ui_element/table/opera/UiTableOpera.vue'
export default {
  components: { PanelDef, UiTableOpera, CpAocDPtwoItems },
