import CardConstructionWork from '../../../../funcks/ui/card/construction/CardConstructionWork.vue'
export default {
  components: { LayoutScreenForm, UiStepTab, PanelDef, CpDrRrDBaseMsg, DrRrDc, CardConstructionWork },
  data() {
    return {
      tab: 1,
      task: [
        { id: 1, txt: '' },
        { id: 2, txt: '' }
      ],
      stat: [
        { id: 1, txt: '' },
        { id: 2, txt: '' }
      ],
    }
  },
  methods: {
    changeTab(v) { this.tab = v }
  }
}
</script>

<style>

